/* Header container styling */
.header {
  @apply sticky top-0 z-50 border-b-2 border-yellow-400 bg-slate-900 shadow-lg;
}

/* Logo container */
.logoContainer {
  @apply flex-shrink-0;
}

.logoButton {
  @apply flex items-center rounded-md transition-opacity duration-200 hover:opacity-80 focus:outline-none focus:ring-2 focus:ring-yellow-400;
}

.logoIcon {
  @apply flex h-10 w-10 items-center justify-center rounded-lg bg-gradient-to-br from-yellow-400 to-green-400 text-xl font-bold text-slate-900;
}

.logoText {
  @apply ml-3 text-xl font-bold text-white;
}

/* Desktop navigation */
.desktopNav {
  @apply hidden md:block;
}

.navList {
  @apply ml-10 flex items-baseline space-x-1;
}

.navButton {
  @apply rounded-md px-4 py-2 text-sm font-medium transition-all duration-200 hover:bg-slate-800 hover:text-yellow-400 focus:outline-none focus:ring-2 focus:ring-yellow-400;
}

.navButtonActive {
  @apply text-slate-900 shadow-md;
}

.navButtonInactive {
  @apply text-gray-200 hover:shadow-md;
}

.responsibleBettingButton {
  @apply border border-yellow-400/30;
}

/* Mobile menu button */
.mobileMenuButton {
  @apply inline-flex items-center justify-center rounded-md p-2 text-gray-200 transition-colors duration-200 hover:bg-slate-800 hover:text-yellow-400 focus:outline-none focus:ring-2 focus:ring-yellow-400 md:hidden;
}

/* Mobile navigation menu */
.mobileMenu {
  @apply md:hidden;
}

.mobileMenuContainer {
  @apply mt-2 space-y-1 rounded-lg border-t-2 border-yellow-400 bg-slate-800 px-2 pb-3 pt-2;
}

.mobileNavButton {
  @apply block w-full rounded-md px-3 py-2 text-left text-base font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-yellow-400;
}

.mobileNavButtonActive {
  @apply text-slate-900;
}

.mobileNavButtonInactive {
  @apply text-gray-200 hover:bg-slate-700 hover:text-yellow-400;
}

.mobileResponsibleBettingButton {
  @apply mt-2 border border-yellow-400/30;
}

/* Animation for mobile menu */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.mobileMenu {
  animation: slideDown 0.2s ease-out;
}

/* Responsive breakpoints */
@media (max-width: 768px) {
  .logoText {
    @apply text-lg;
  }

  .logoIcon {
    @apply h-8 w-8 text-lg;
  }
}
