/* Footer.module.css */

/* Main footer container */
.footer {
  @apply border-t-2 border-yellow-400 bg-slate-900 text-gray-300;
}

.footerContainer {
  @apply mx-auto max-w-7xl px-4 sm:px-6 lg:px-8;
}

/* Brand section */
.brandSection {
  @apply lg:col-span-1;
}

.brandLogo {
  @apply mb-4 flex items-center;
}

.logoIcon {
  @apply flex h-10 w-10 items-center justify-center rounded-lg bg-gradient-to-br from-yellow-400 to-green-400 text-xl font-bold text-slate-900;
}

.logoText {
  @apply ml-3 text-xl font-bold text-white;
}

.brandDescription {
  @apply mb-4 text-sm leading-relaxed text-gray-400;
}

/* Social links */
.socialLinks {
  @apply flex space-x-4;
}

.socialButton {
  @apply rounded-md bg-slate-800 p-2 text-gray-400 transition-all duration-200 hover:bg-yellow-400 hover:text-slate-900 focus:outline-none focus:ring-2 focus:ring-yellow-400;
}

/* Footer sections */
.footerSection {
  /* Applied to each column div */
}

.sectionTitle {
  @apply mb-4 font-semibold text-white;
}

.sectionTitleWithIcon {
  @apply mb-4 flex items-center font-semibold text-white;
}

.sectionIcon {
  @apply mr-2 h-4 w-4 text-yellow-400;
}

/* Link lists */
.linkList {
  @apply space-y-2;
}

.linkButton {
  @apply rounded text-sm text-gray-400 transition-colors duration-200 hover:text-yellow-400 focus:outline-none focus:ring-2 focus:ring-yellow-400;
}

.externalLinkButton {
  @apply flex items-center rounded text-sm text-gray-400 transition-colors duration-200 hover:text-yellow-400 focus:outline-none focus:ring-2 focus:ring-yellow-400;
}

.externalIcon {
  @apply ml-1 h-3 w-3;
}

/* Responsible gambling banner */
.responsibleGamblingBanner {
  @apply border-t border-slate-800 py-6;
}

.responsibleGamblingCard {
  @apply rounded-lg border border-yellow-400/20 bg-slate-800 p-4;
}

.responsibleGamblingContent {
  @apply flex items-start space-x-3;
}

.responsibleGamblingIcon {
  @apply flex-shrink-0;
}

.responsibleGamblingText {
  @apply flex-1;
}

.responsibleGamblingTitle {
  @apply mb-1 text-sm font-semibold text-yellow-400;
}

.responsibleGamblingDescription {
  @apply text-xs leading-relaxed text-gray-400;
}

.responsibleGamblingLinks {
  @apply mt-2 flex flex-wrap gap-2 text-xs;
}

.responsibleGamblingLink {
  @apply rounded text-yellow-400 transition-colors duration-200 hover:text-yellow-300 focus:outline-none focus:ring-2 focus:ring-yellow-400;
}

.linkSeparator {
  @apply text-gray-500;
}

/* Copyright section */
.copyrightSection {
  @apply border-t border-slate-800 py-6;
}

.copyrightContent {
  @apply flex flex-col items-center justify-between text-sm text-gray-500 md:flex-row;
}

.copyrightText {
  @apply mb-2 md:mb-0;
}

.featuresList {
  @apply flex items-center space-x-4 text-xs;
}

.featureSeparator {
  @apply hidden md:inline;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .brandLogo {
    @apply justify-center md:justify-start;
  }

  .socialLinks {
    @apply justify-center md:justify-start;
  }

  .sectionTitle,
  .sectionTitleWithIcon {
    @apply text-center md:text-left;
  }

  .linkList {
    @apply text-center md:text-left;
  }

  .responsibleGamblingContent {
    @apply flex-col space-x-0 space-y-3 md:flex-row md:space-x-3 md:space-y-0;
  }

  .responsibleGamblingIcon {
    @apply flex justify-center md:justify-start;
  }

  .responsibleGamblingLinks {
    @apply justify-center md:justify-start;
  }
}

/* Hover animations */
.linkButton:hover,
.externalLinkButton:hover,
.responsibleGamblingLink:hover {
  transform: translateX(2px);
}

.socialButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 255, 0, 0.3);
}

/* Focus states for accessibility */
.linkButton:focus,
.externalLinkButton:focus,
.socialButton:focus,
.responsibleGamblingLink:focus {
  outline: 2px solid #facc15;
  outline-offset: 2px;
}
